'use client';

import { createContext, useContext, useEffect, useState, ReactNode } from 'react';
import { Alert } from '@/components/ui/Alert';

interface User {
  id: string;
  email: string;
  firstName: string;
  lastName: string;
  avatar?: string;
  role: string;
  phone?: string;
  location?: string;
  company?: string;
  jobTitle?: string;
  bio?: string;
  linkedIn?: string;
  github?: string;
  website?: string;
  createdAt?: string;
  subscription: {
    tier: 'free' | 'pro' | 'enterprise';
    nextBillingDate?: string;
    usage: {
      sessionsUsed: number;
      sessionsLimit?: number;
    };
  };
  settings?: {
    notifications?: {
      emailNotifications: boolean;
      practiceReminders: boolean;
      progressUpdates: boolean;
      marketingEmails: boolean;
    };
    privacy?: {
      dataCollection: boolean;
      analyticsTracking: boolean;
      shareProgress: boolean;
      publicProfile: boolean;
    };
    media?: {
      defaultMicrophone: string;
      defaultCamera: string;
      defaultSpeaker: string;
      microphoneGain: number;
      cameraResolution: string;
      speakerVolume: number;
    };
    theme?: string;
    language?: string;
  };
}

interface AuthContextType {
  user: User | null;
  loading: boolean;
  login: (email: string, password: string, rememberMe?: boolean) => Promise<void>;
  register: (userData: RegisterData) => Promise<void>;
  logout: () => Promise<void>;
  resetPassword: (email: string) => Promise<void>;
  refreshSession: () => Promise<void>;
  updateProfile: (profileData: any) => Promise<void>;
  updateSettings: (settings: any) => Promise<void>;
  upgradePlan: (planId: string) => Promise<void>;
  deleteAccount: () => Promise<void>;
  isAuthenticated: boolean;
}

interface RegisterData {
  firstName: string;
  lastName: string;
  email: string;
  password: string;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export function useAuth() {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}

interface AuthProviderProps {
  children: ReactNode;
}

export function AuthProvider({ children }: AuthProviderProps) {
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);
  const [sessionExpired, setSessionExpired] = useState(false);

  // Enhanced authentication with proper password validation
  const login = async (email: string, password: string, rememberMe = false) => {
    setLoading(true);
    try {
      // Simulate API call with proper validation
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      // Mock user database with hashed passwords (in real app, this would be server-side)
      const mockUsers = [
        {
          id: '1',
          email: 'john@example.com',
          // This would be a bcrypt hash in real implementation
          passwordHash: 'hashed_password_123',
          firstName: 'John',
          lastName: 'Doe',
          role: 'user',
          subscription: {
            tier: 'free' as const,
            usage: {
              sessionsUsed: 3,
              sessionsLimit: 5,
            },
          },
          createdAt: '2024-01-01T00:00:00Z',
        },
        {
          id: '2',
          email: 'demo@example.com',
          passwordHash: 'demo_password_hash',
          firstName: 'Demo',
          lastName: 'User',
          role: 'user',
          subscription: {
            tier: 'pro' as const,
            nextBillingDate: '2024-02-01T00:00:00Z',
            usage: {
              sessionsUsed: 8,
              sessionsLimit: undefined, // Unlimited for pro
            },
          },
          createdAt: '2023-12-01T00:00:00Z',
        }
      ];
      
      // Find user by email
      const foundUser = mockUsers.find(u => u.email.toLowerCase() === email.toLowerCase());
      
      if (!foundUser) {
        throw new Error('Invalid email or password');
      }
      
      // Simulate password verification (in real app, use bcrypt.compare)
      const isValidPassword = await verifyPassword(password, foundUser.passwordHash);
      
      if (!isValidPassword) {
        // Implement rate limiting check
        await checkRateLimit(email);
        throw new Error('Invalid email or password');
      }
      
      // Create user object without password hash
      const { passwordHash, ...userWithoutPassword } = foundUser;
      const authenticatedUser: User = userWithoutPassword;
      
      setUser(authenticatedUser);
      
      // Store session info
      const sessionData = {
        user: authenticatedUser,
        expiresAt: rememberMe 
          ? Date.now() + 30 * 24 * 60 * 60 * 1000 // 30 days
          : Date.now() + 24 * 60 * 60 * 1000, // 24 hours
      };
      
      localStorage.setItem('auth_session', JSON.stringify(sessionData));
      
      // Clear any rate limiting for successful login
      clearRateLimit(email);
      
    } catch (error) {
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // Mock password verification (in real app, use bcrypt)
  const verifyPassword = async (password: string, hash: string): Promise<boolean> => {
    // Simulate password hashing verification
    await new Promise(resolve => setTimeout(resolve, 100));
    
    // For demo purposes, accept specific passwords
    const validPasswords: Record<string, string> = {
      'hashed_password_123': 'password123',
      'demo_password_hash': 'demo123'
    };
    
    return validPasswords[hash] === password;
  };

  // Rate limiting implementation
  const checkRateLimit = async (email: string) => {
    const rateLimitKey = `rate_limit_${email}`;
    const rateLimitData = localStorage.getItem(rateLimitKey);
    
    if (rateLimitData) {
      const { attempts, lastAttempt } = JSON.parse(rateLimitData);
      const now = Date.now();
      const timeSinceLastAttempt = now - lastAttempt;
      
      // Reset attempts after 15 minutes
      if (timeSinceLastAttempt > 15 * 60 * 1000) {
        localStorage.removeItem(rateLimitKey);
        return;
      }
      
      // Check if rate limited
      if (attempts >= 5) {
        const remainingTime = Math.ceil((15 * 60 * 1000 - timeSinceLastAttempt) / 60000);
        throw new Error(`Too many failed attempts. Please try again in ${remainingTime} minutes.`);
      }
      
      // Increment attempts
      localStorage.setItem(rateLimitKey, JSON.stringify({
        attempts: attempts + 1,
        lastAttempt: now
      }));
    } else {
      // First failed attempt
      localStorage.setItem(rateLimitKey, JSON.stringify({
        attempts: 1,
        lastAttempt: Date.now()
      }));
    }
  };

  const clearRateLimit = (email: string) => {
    const rateLimitKey = `rate_limit_${email}`;
    localStorage.removeItem(rateLimitKey);
  };

  const register = async (userData: RegisterData) => {
    setLoading(true);
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      // Mock successful registration
      const mockUser: User = {
        id: '1',
        email: userData.email,
        firstName: userData.firstName,
        lastName: userData.lastName,
        role: 'user',
        subscription: {
          tier: 'free',
          usage: {
            sessionsUsed: 0,
            sessionsLimit: 5,
          },
        },
      };
      
      setUser(mockUser);
      
      // Store session info
      const sessionData = {
        user: mockUser,
        expiresAt: Date.now() + 24 * 60 * 60 * 1000, // 24 hours
      };
      
      localStorage.setItem('auth_session', JSON.stringify(sessionData));
      
    } catch (error) {
      throw new Error('Registration failed. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const logout = async () => {
    setLoading(true);
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 500));
      
      setUser(null);
      localStorage.removeItem('auth_session');
      setSessionExpired(false);
      
    } catch (error) {
      console.error('Logout error:', error);
    } finally {
      setLoading(false);
    }
  };

  const resetPassword = async (email: string) => {
    setLoading(true);
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      // In real app, this would send reset email
      console.log('Password reset email sent to:', email);
      
    } catch (error) {
      throw new Error('Failed to send reset email. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const refreshSession = async () => {
    try {
      const sessionData = localStorage.getItem('auth_session');
      if (!sessionData) return;
      
      const { user: storedUser, expiresAt } = JSON.parse(sessionData);
      
      if (Date.now() > expiresAt) {
        // Session expired
        localStorage.removeItem('auth_session');
        setSessionExpired(true);
        setUser(null);
        return;
      }
      
      // Simulate API call to refresh session
      await new Promise(resolve => setTimeout(resolve, 500));
      
      setUser(storedUser);
      
    } catch (error) {
      console.error('Session refresh error:', error);
      localStorage.removeItem('auth_session');
      setUser(null);
    }
  };

  const updateProfile = async (profileData: any) => {
    setLoading(true);
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      if (user) {
        const updatedUser = { ...user, ...profileData };
        setUser(updatedUser);
        
        // Update stored session
        const sessionData = localStorage.getItem('auth_session');
        if (sessionData) {
          const session = JSON.parse(sessionData);
          session.user = updatedUser;
          localStorage.setItem('auth_session', JSON.stringify(session));
        }
      }
    } catch (error) {
      throw new Error('Failed to update profile. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const updateSettings = async (settings: any) => {
    setLoading(true);
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      if (user) {
        const updatedUser = { 
          ...user, 
          settings: { ...user.settings, ...settings }
        };
        setUser(updatedUser);
        
        // Update stored session
        const sessionData = localStorage.getItem('auth_session');
        if (sessionData) {
          const session = JSON.parse(sessionData);
          session.user = updatedUser;
          localStorage.setItem('auth_session', JSON.stringify(session));
        }
      }
    } catch (error) {
      throw new Error('Failed to update settings. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const upgradePlan = async (planId: string) => {
    setLoading(true);
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      if (user) {
        const planLimits: Record<string, any> = {
          free: { sessionsLimit: 5 },
          pro: { sessionsLimit: undefined }, // Unlimited
          enterprise: { sessionsLimit: undefined } // Unlimited
        };
        
        const updatedUser = { 
          ...user, 
          subscription: {
            ...user.subscription,
            tier: planId as 'free' | 'pro' | 'enterprise',
            usage: {
              ...user.subscription.usage,
              ...planLimits[planId]
            },
            nextBillingDate: planId !== 'free' ? new Date(Date.now() + 30 * 24 * 60 * 60 * 1000).toISOString() : undefined
          }
        };
        setUser(updatedUser);
        
        // Update stored session
        const sessionData = localStorage.getItem('auth_session');
        if (sessionData) {
          const session = JSON.parse(sessionData);
          session.user = updatedUser;
          localStorage.setItem('auth_session', JSON.stringify(session));
        }
      }
    } catch (error) {
      throw new Error('Failed to upgrade plan. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const deleteAccount = async () => {
    setLoading(true);
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      // Clear all data
      setUser(null);
      localStorage.removeItem('auth_session');
      
      // In a real app, this would redirect to a goodbye page
      window.location.href = '/';
    } catch (error) {
      throw new Error('Failed to delete account. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  // Check for existing session on mount
  useEffect(() => {
    const checkSession = async () => {
      setLoading(true);
      await refreshSession();
      setLoading(false);
    };
    
    checkSession();
  }, []);

  // Set up session refresh interval
  useEffect(() => {
    if (user) {
      const interval = setInterval(() => {
        refreshSession();
      }, 5 * 60 * 1000); // Check every 5 minutes
      
      return () => clearInterval(interval);
    }
  }, [user]);

  const value: AuthContextType = {
    user,
    loading,
    login,
    register,
    logout,
    resetPassword,
    refreshSession,
    updateProfile,
    updateSettings,
    upgradePlan,
    deleteAccount,
    isAuthenticated: !!user,
  };

  return (
    <AuthContext.Provider value={value}>
      {sessionExpired && (
        <div className="fixed top-4 right-4 z-50 max-w-md">
          <Alert variant="warning" title="Session Expired">
            Your session has expired. Please sign in again to continue.
          </Alert>
        </div>
      )}
      {children}
    </AuthContext.Provider>
  );
}